<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">助力池</div>
      <div class="rightButton">
        <!-- <el-input v-model="searchForm.url" placeholder="url" clearable></el-input> -->
        <el-button type="primary" size="mini" @click="addCk(1)">
          添加ck</el-button
        >
      </div>
    </div>

    <div class="mainMseeage">
      <el-form :inline="true" :model="formInline" class="searchForm">
        <el-form-item label="ck">
          <el-input
            v-model="formInline.ck"
            placeholder="ck"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="ptPin">
          <el-input
            v-model="formInline.ptPin"
            placeholder="ptPin"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="level">
          <el-select
            v-model="formInline.level"
            placeholder="请选择level"
            clearable
          >
            <el-option
              v-for="item in levelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="status">
          <el-select
            v-model="formInline.status"
            placeholder="请选择status"
            clearable
          >
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>

      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column prop="ck" label="京东ck" width="" align="center">
        </el-table-column>
        <el-table-column prop="ptPin" label="ptPin" width="200" align="center">
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="200" align="center">
        </el-table-column>
        <el-table-column label="level" width="100" align="center">
          <template slot-scope="scoped">
            {{ scoped.row.level | level }}
          </template>
        </el-table-column>
        <el-table-column prop="expiryTime" label="过期时间" width="200" align="center">
        </el-table-column>
        <el-table-column label="是否有效" width="100" align="center">
          <template slot-scope="scoped">
            {{ scoped.row.status | status }}
          </template>
        </el-table-column>
        <el-table-column
          prop="jd"
          label="前一天收益"
          width="120"
          align="center"
        >
        </el-table-column>
        <el-table-column label="操作" align="center" width="120" fixed="right">
          <template slot-scope="scoped">
            <el-button type="text" @click="addCk(2, scoped.row)"
              >修改</el-button
            >
            <el-button
              type="text"
              :loading="scoped.row.delLoading"
              @click="deleteCK(scoped.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 添加ck -->
    <el-dialog
      :title="ckName"
      :visible.sync="addVisible"
      ref="ckvis"
      width="30%"
      @closed="addClose('addForm')"
    >
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addForm"
        label-width="100px"
      >
        <el-form-item label="京东ck" prop="ck">
          <el-input
            v-model="addForm.ck"
            placeholder="请输入ck"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="level" prop="level">
          <el-select
            v-model="addForm.level"
            placeholder="请选择level"
            clearable
          >
            <el-option
              v-for="item in levelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input
            v-model="addForm.remark"
            placeholder="请输入备注"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="过期时间" prop="expiryTime">
          <el-date-picker
            v-model="addForm.expiryTime"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button
          type="primary"
          :loading="sureLoading"
          @click="sureAdd('addForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  addCk,
  getJdCks,
  updateJdCk,
  deleteJdCks,
} from "../axiosRequest/allRequest";

export default {
  filters: {
    level(value) {
      const obj = {
        0: "永久",
        1: "vip",
        2: "普通",
        3: "工具人",
      };
      return obj[value];
    },
    status(value) {
      const obj = {
        0: "有效",
        1: "无效",
      };
      return obj[value];
    },
  },
  data() {
    return {
      formInline: {},
      tableData: [],
      ckName: "添加CK",
      addVisible: false,
      addForm: {
        ck: "",
        remark: "",
        level: "",
        expiryTime: "",
        id: "",
      },
      addRules: {
        ck: [{ required: true, message: "请输入ck", trigger: "blur" }],
        remark: [],
        level: [{ required: true, message: "请选择level", trigger: "blur" }],
        expiryTime: [],
        id: [],
      },
      levelOptions: [
        {
          value: 0,
          label: "永久",
        },
        {
          value: 1,
          label: "vip",
        },
        {
          value: 2,
          label: "普通",
        },
        {
          value: 3,
          label: "工具人",
        },
      ],
      statusOptions: [
        {
          value: 0,
          label: "有效",
        },
        {
          value: 1,
          label: "无效",
        },
      ],
      sureLoading: false,
    };
  },

  created() {
    this.onSubmit();
  },
  methods: {
    addCk(i, row) {
      this.addVisible = true;
      if (i == 1) {
        this.ckName = "添加CK";
      } else {
        this.ckName = "修改CK";
      }

      if (row) {
        this.addForm.ck = row.ck;
        this.addForm.remark = row.remark;
        this.addForm.level = row.level;
        this.addForm.expiryTime = row.expiryTime;
        this.addForm.id = row.id;
      }
    },

    sureAdd(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true;
          if (this.ckName == "添加CK") {
            addCk(this.addForm).then((res) => {
              this.sureLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("添加成功");
                this.addVisible = false;
                this.onSubmit();
              }
            });
          } else {
            updateJdCk(this.addForm).then((res) => {
              this.sureLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("修改成功");
                this.addVisible = false;
                this.onSubmit();
              }
            });
          }
        } else {
          return false;
        }
      });
    },

    addClose(formName) {
      this.$refs[formName].resetFields();
    },

    onSubmit() {
      getJdCks(this.formInline).then((res) => {
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          let a = JSON.parse(JSON.stringify(res.data));
          a.forEach((item, index) => {
            this.$set(item, "delLoading", false);
          });
          this.tableData = a;
        }
      });
    },

    deleteCK(row) {
      this.$confirm("确认删除此CK吗？", "删除！", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.tableData.forEach((item) => {
          if (row.id == item.id) {
            this.$set(item, "delLoading", true);
          }
        });
        deleteJdCks([row.id]).then((res) => {
          this.tableData.forEach((item) => {
            if (row.id == item.id) {
              this.$set(item, "delLoading", false);
            }
          });
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success("删除成功");
            this.onSubmit();
          }
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.searchForm {
  background-color: #fff;
  padding: 10px;
  margin-bottom: 5px;
  .el-form-item {
    margin-bottom: 0;
  }
}
</style>