<template>
  <div id="backgroundImages">
    <div class="jdtopTitle">别闹京东助力池</div>
    <div class="mainBox">
      <div class="leftM">
        <div class="echartsBox" id="echartsBox"></div>
        <div class="ckBox">
          <h3>今 日 详 情</h3>
          <div class="maxMsg">
            <div>助力池ck ：{{ jdInfoList.allCkCount }}</div>
            <div>东东农场:{{ jdInfoList.jdfruit }}</div>
            <div>东东萌宠:{{ jdInfoList.jdpet }}</div>
            <div>种豆得豆:{{ jdInfoList.jdbean }}</div>
            <div>东东工厂:{{ jdInfoList.jdddfactory }}</div>
            <div>健康社区:{{ jdInfoList.jdhealth }}</div>
            <div>京喜工厂:{{ jdInfoList.jdjxfactory }}</div>
            <div>闪购盲盒:{{ jdInfoList.jdsgmh }}</div>
          </div>
        </div>
      </div>
      <div class="rightM">
        <div class="rightTop">
          <div style="font-size: 20px; color: blue; padding-bottom: 5px">
            助力规则
          </div>
          <h2>每日随机排序所有账号，依次助力</h2>
          <br />
          <h2 style="text-align: left">东东农场：</h2>
          <h3 style="text-align: left">
            &nbsp;&nbsp;&nbsp;&nbsp;每个号可助力3次，需要被助力8次
          </h3>
          <h2 style="text-align: left">东东萌宠：</h2>
          <h3 style="text-align: left">
            &nbsp;&nbsp;&nbsp;&nbsp;每个号可助力5次，需要被助力5次
          </h3>
          <h2 style="text-align: left">种豆得豆：</h2>
          <h3 style="text-align: left">
            &nbsp;&nbsp;&nbsp;&nbsp;每个号可助力3次，需要被助力9次
          </h3>
        </div>
        <div class="rightCenter">
          <div class="subBox">
            <span class="subMax">提交助力池</span>
            <el-input
              class="ckInput"
              v-model="form.ck"
              placeholder="请输入你的ck"
            ></el-input>
            <div style="text-align: center; margin: 10px">
              <el-button
                type="primary"
                :loading="submitLoading"
                @click="onSubmit"
                >提交</el-button
              >
              <div style="font-size: 15px; color: red; margin-top: 20px">
                tip:点击下方代挂则无需再次添加助力池
              </div>
            </div>
          </div>
        </div>
        <div class="rightBottom">
          <!-- <el-button type="primary" @click="jumpJD">京东代挂登录</el-button> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Message } from "element-ui";
import { addCk, getJdInfo } from "../axiosRequest/allRequest";
import * as echarts from "echarts";
export default {
  data() {
    return {
      form: {
        ck: "",
      },
      jdInfoList: {},
      submitLoading: false,
      option: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
          },
        },
        backgroundColor: "rgba(128,128,128,0.5)",

        legend: {
          data: ["京豆七天收益"],
        },
        xAxis: {
          type: "category",
          data: [],
          axisLine: {
            show: true,
            lineStyle: {
              color: "black",
            },
          },
        },
        yAxis: {
          type: "value",
          axisLine: {
            show: true,
            lineStyle: {
              color: "black",
            },
          },
        },
        series: [
          {
            data: [],
            type: "line",
            name: "京豆七天收益",
            showBackground: true,
            backgroundStyle: {
              color: "rgba(180, 180, 180, 0.2)",
            },
            itemStyle: {
              normal: {
                label: {
                  show: true, //开启显示
                  position: "top", //在上方显示
                  textStyle: {
                    //数值样式
                    color: "black",
                    fontSize: 16,
                  },
                },
              },
            },
          },
        ],
      },
    };
  },

  created() {
    this.getInfo();
  },

  mounted() {},

  methods: {
    onSubmit() {
      this.submitLoading = true;
      addCk({ ck: this.form.ck, level: 2 }).then((res ) => {
        console.log(res,'res');
        this.submitLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          Message.success("成功！");
          this.form.ck = "";
        }
      });
    },
    jumpJD() {
      if (this.jdInfoList.jdLoginUrl) {
        window.open(this.jdInfoList.jdLoginUrl);
      } else {
        Message.warning("未获取到跳转链接");
      }
    },
    getInfo() {
      getJdInfo().then((res) => {
        console.log(res, "ssss");
        if (res.code == 200) {
          let a = JSON.parse(JSON.stringify(res.data));
          this.jdInfoList = a;
          this.option.series[0].data = [];
          if (a.jdIncome) {
            for (let i in a.jdIncome) {
              this.option.xAxis.data.push(i);
              this.option.series[0].data.push(a.jdIncome[i]);
            }
          } else {
            this.option.series[0].data = [0, 0, 0, 0, 0, 0, 0];
          }
          var myChart = echarts.init(document.getElementById("echartsBox"));
          myChart.setOption(this.option);
          window.addEventListener("resize", () => {
            if (myChart) {
              myChart.resize();
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },
  },
};
</script>

<style scope lang="less">
#backgroundImages {
  min-height: 600px;
  width: 100%;
  height: 100vh;
  background: url("https://i.imgtg.com/2022/07/24/r1VMD.png");
  background-size: 100% 100% cover;
  overflow: hidden;
}

.el-input__inner {
  background: rgba(255, 255, 255, 0.1) !important;
}

.jdtopTitle {
  margin: 20px 0px;
  color: blue;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
}

@media screen and (min-width: 900px) and (max-width: 1920px) {
  .mainBox {
    height: 90vh;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    .leftM {
      flex: 8;
      overflow: hidden;

      .echartsBox {
        padding-right: 30%;
        height: 40vh;
      }

      .ckBox {
        text-align: center;
        margin-right: 30%;
        margin-top: 20px;
        padding: 10px;
        height: 40vh;
        overflow-y: auto;
        // background-color: rgba(128,128,128,0.5);
        color: blue;
        // font-size: 30px;
        font-size: calc(100vw * 25 / 1920);
      }
    }

    .rightM {
      flex: 2;
      height: 90vh;
      padding: 0 20px;
      display: flex;
      flex-direction: column;
      text-align: center;

      h2 {
        font-size: 1.15em;
      }

      h3 {
        font-size: 0.8em;
      }

      .rightTop {
        height: 45vh;
        white-space: normal;
        word-break: break-all;
        overflow: hidden;
        background-color: rgba(221, 220, 235, 0.5);
        margin-bottom: 5px;
        border-radius: 5px;
      }

      .rightCenter {
        height: 38vh;
        margin-bottom: 0.3125rem;

        display: flex;
        align-items: center;

        .subBox {
          background-color: rgba(221, 220, 235, 0.5);
          margin: auto;
          width: 100%;
          border-radius: 5px;
          padding: 30px 0px;

          .subMax {
            color: blue;
            font-size: calc(100vw * 30 / 1920);
          }
        }
      }

      .rightBottom {
        height: 10vh;
        // background-color: rgba(221, 220, 235, 0.5);
        border-radius: 5px;
        display: flex;
        align-items: center;

        .el-button {
          margin: auto;
          // font-size: 30px;
          font-size: calc(100vw * 30 / 1920);
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .mainBox {
    .leftM {
      .echartsBox {
        height: 200px;
      }

      .ckBox {
        margin: 10px 0;
        color: blue;

        .maxMsg {
          font-size: 16px;
        }
      }
    }

    .rightM {
      .rightTop {
        background-color: rgba(221, 220, 235, 0.5);
        border-radius: 5px;
        padding: 2px;
      }

      .rightCenter {
        .subBox {
          padding: 10px 0;
          background-color: rgba(221, 220, 235, 0.5);
          margin: 10px 0;

          .subMax {
            color: blue;
            font-size: 30px;
          }
        }
      }

      .rightBottom {
        margin-bottom: 10px;
      }
    }
  }
}
</style>