<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">
        {{  scriptTitle ? scriptTitle : "请选择脚本文件"  }}
      </div>
      <div class="rightButton" v-if="editorEdit">
        <el-tooltip class="item" effect="dark" content="新建" placement="bottom">
          <el-button type="primary" size="mini" @click="addNewScript"><i class="el-icon-circle-plus-outline"></i>
          </el-button>
        </el-tooltip>

        <el-tooltip class="item" effect="dark" content="编辑" placement="bottom">
          <el-button type="primary" size="mini" :disabled="scriptTitle ? false : true" @click="editScript"><i
              class="el-icon-edit"></i>
          </el-button>
        </el-tooltip>

        <el-tooltip class="item" effect="dark" content="删除" placement="bottom">
          <el-button type="primary" size="mini" :disabled="scriptTitle ? false : true" @click="deleteScript"><i
              class="el-icon-delete"></i></el-button>
        </el-tooltip>
        <el-button type="primary" size="mini" @click="debugScript">调试</el-button>
      </div>

      <div class="rightButton" v-else>
        <el-button type="primary" size="mini" :loading="saveEditLoading" @click="saveEdit">保存</el-button>
        <el-button type="primary" size="mini" @click="returnEdit">退出编辑</el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <div class="leftMeun">
        <el-input placeholder="请输入内容" v-model="searchScript" class="input-with-select">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <el-tree :data="MeunData" :props="defaultProps" :default-expand-all=false @node-click="handleNodeClick">
        </el-tree>
      </div>
      <div class="rightCode">
        <prism-editor class="my-editor height-300" :readonly="editorEdit" v-model="code" :highlight="highlighter"
          :line-numbers="lineNumbers">
        </prism-editor>
      </div>
    </div>

    <el-dialog title="新建文件" :visible.sync="addNewVisible" width="30%" @closed="addNewClose('addNewForm')">
      <el-form :model="addNewForm" :rules="addNewRule" ref="addNewForm" class="demo-addNewForm">
        <el-form-item label="文件名" prop="fileNname">
          <el-input v-model="addNewForm.fileNname" placeholder="请输入文件名" clearable></el-input>
        </el-form-item>
        <el-form-item label="父目录" prop="ParentDirectory">
          <el-select v-model="addNewForm.ParentDirectory" placeholder="请选择父目录" clearable>
            <el-option v-for="item in ParentDirectoryList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addNewVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureAdd('addNewForm')">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 调试抽屉 -->
    <el-drawer :with-header="false" :visible.sync="debugging" direction="rtl">
      <div class="buttonBox">
        <el-button type="primary">运行</el-button>
        <el-button type="primary">清空日志</el-button>
        <el-button type="primary">设置</el-button>
        <el-button type="primary">保存</el-button>
        <el-button type="primary" @click="outDebug">退出</el-button>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import { PrismEditor } from "vue-prism-editor";
import "vue-prism-editor/dist/prismeditor.min.css"; // import the styles somewhere

// import highlighting library (you can use any library you want just return html string)
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";
import "prismjs/themes/prism-tomorrow.css"; // import syntax highlighting styles

export default {
  components: {
    PrismEditor,
  },
  data() {
    return {
      editorEdit: true,
      addNewVisible: false,
      saveEditLoading: false,
      scriptTitle: "",
      searchScript: "",
      debugging: false,
      defaultProps: {
        children: "children",
        label: "label",
      },
      MeunData: [
        {
          label: "一级 1",
          children: [
            {
              label: "二级 1-1",
              children: [
                {
                  label: "三级 1-1-1",
                },
              ],
            },
          ],
        },
        {
          label: "一级 2",
          children: [
            {
              label: "二级 2-1",
              children: [
                {
                  label: "三级 2-1-1",
                },
              ],
            },
            {
              label: "二级 2-2",
              children: [
                {
                  label: "三级 2-2-1",
                },
              ],
            },
          ],
        },
        {
          label: "一级 3",
          children: [
            {
              label: "二级 3-1",
              children: [
                {
                  label: "三级 3-1-1",
                },
              ],
            },
            {
              label: "二级 3-2",
              children: [
                {
                  label: "三级 3-2-1",
                },
              ],
            },
          ],
        },
      ],
      code: 'console.log("Hello World")',
      lineNumbers: true,
      addNewForm: {
        fileNname: '',
        ParentDirectory: ''
      },
      addNewRule: {
        fileNname: [
          { required: true, message: '请输入文件名', trigger: 'blur' }
        ],
        ParentDirectory: []
      },
      ParentDirectoryList: [
        { value: '1', label: '1' },
        { value: '2', label: '2' },
        { value: '3', label: '3' },
      ]
    };
  },
  created() { },
  mounted() { },
  methods: {
    highlighter(code) {
      return highlight(code, languages.js); //returns html
    },
    handleNodeClick(val, e) {
      //tree菜单
      if (e.level == 3) {
        this.scriptTitle = e.label
        //code内容根据菜单赋值
      }
    },
    addNewScript() {
      this.addNewVisible = true
    },
    sureAdd(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          return false;
        }
      });
    },
    addNewClose(formName) {
      this.$refs[formName].resetFields();
    },
    editScript() {
      this.editorEdit = false
      //获取初始code时新建变量保存原值以便取消编辑
    },
    returnEdit() {
      this.editorEdit = true
      //将code只会原样
    },
    saveEdit() {
      //保存编辑  同时要将保存的原先的值一起改变  可以通过接口返回的值重新赋值
      this.saveEditLoading = true
      setTimeout(() => {
        this.saveEditLoading = false
      }, 2000)
    },
    deleteScript() {
      this.$confirm(`确认删除文件${this.scriptTitle}?删除后不可恢复`, '确认删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      })
    },
    debugScript() {
      this.debugging = true
    },
    outDebug() {
      this.debugging = false
    }
  },
};
</script>

<style lang="less" scoped>
.mainMseeage {
  display: flex;

  .leftMeun {
    padding: 10px;
    width: 20%;
    height: 100%;
    background-color: white;
    overflow-y: auto;
  }

  .rightCode {
    width: 80%;
    height: 100%;
    background-color: aquamarine;
  }
}

::v-deep .rtl {
  width: 100% !important;
}

.buttonBox {
  padding: 10px;
  border-bottom: 1px solid gray;
}

.my-editor {
  background: #2d2d2d;
  color: #ccc;
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px;
}

/* optional */
.prism-editor__textarea:focus {
  outline: none;
}

/* not required: */
.height-300 {
  height: 100%;
}
</style>