import { request } from './request'
import qs from "qs";

//登录
export function login(data) {
    return request({
        method: 'post',
        url: '/robot/token/login',
        data: data
    })
}

//注册
export function check(data) {
    return request({
        method: 'post',
        url: '/robot/token/register',
        data: data
    })
}


