<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">config.sh</div>
      <div class="rightButton">
        <el-select style="margin-right:10px" v-model="configName" placeholder="请选择">
          <el-option v-for="item in configList" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary" size="mini">保存</el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <prism-editor class="my-editor height-300" v-model="code" :highlight="highlighter" :line-numbers="lineNumbers">
      </prism-editor>
    </div>
  </div>
</template>
<script>
import { PrismEditor } from "vue-prism-editor";
import "vue-prism-editor/dist/prismeditor.min.css"; // import the styles somewhere

// import highlighting library (you can use any library you want just return html string)
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";
import "prismjs/themes/prism-tomorrow.css"; // import syntax highlighting styles

export default {
  components: {
    PrismEditor,
  },
  data() {
    return {
      code: 'console.log("Hello World")',
      lineNumbers: true,
      configName: '',
      configList:[
        {value:'1',label:'1'},
        {value:'2',label:'2'},
        {value:'3',label:'3'},
      ]
    };
  },
  created() { },
  mounted() { },
  methods: {
    highlighter(code) {
      return highlight(code, languages.js); //returns html
    },
  },
};
</script>

<style lang="less" scoped>
/* required class css样式必写，不然编辑器没有样式，只是纯白页面展示
“height-300” 是给编辑器设置高度的，高度可自行设置，也可以不设置，这个样式非必需*/
.my-editor {
  background: #2d2d2d;
  color: #ccc;
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px;
}

/* optional */
.prism-editor__textarea:focus {
  outline: none;
}

/* not required: */
.height-300 {
  height: 100%;
}
</style>