<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">系统设置</div>
    </div>

    <div class="mainMseeage">
      <el-card>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="安全设置" name="1">
            <div
              style="border-bottom: 0.5px solid #f0f0f0; padding-bottom: 10px"
            >
              修改用户名密码
            </div>
            <el-form
              :model="loginForm"
              :rules="loginRules"
              ref="loginForm"
              class="loginForm"
            >
              <el-form-item label="用户名" prop="account">
                <el-input
                  v-model="loginForm.account"
                  clearable
                  placeholder="用户名"
                ></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input
                  v-model="loginForm.password"
                  clearable
                  placeholder="密码"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="saveLogin('loginForm')"
                  >立即创建</el-button
                >
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="应用设置" name="2">
            <el-table
              v-if="activeName == 2"
              :data="applySettingsData"
              style="width: 100%"
              stripe
            >
              <el-table-column
                prop="name"
                label="名称"
                width="200"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="ClientID"
                label="Client ID"
                width="200"
                align="center"
              >
                <template slot-scope="scoped">
                  {{ scoped.row.ClientID
                  }}<i
                    class="el-icon-document-copy copyIcon"
                    @click="copyMesg(scoped.row.ClientID)"
                  ></i>
                </template>
              </el-table-column>
              <el-table-column
                prop="ClientSecret"
                label="Client Secret"
                width="200"
                align="center"
              >
                <template slot-scope="scoped">
                  *******<i
                    class="el-icon-document-copy copyIcon"
                    @click="copyMesg(scoped.row.ClientSecret)"
                  ></i>
                </template>
              </el-table-column>
              <el-table-column prop="jurisdiction" label="权限" align="center">
                <template slot-scope="scoped">
                  <el-tag
                    class="jurisdictionTag"
                    v-for="(item, index) in scoped.row.jurisdiction"
                    :key="index"
                    type="info"
                  >
                    {{ item.a }}</el-tag
                  >
                </template>
              </el-table-column>
              <el-table-column prop="" label="操作" width="200" align="center">
                <template slot-scope="scoped">
                  <el-tooltip effect="dark" content="编辑" placement="top">
                    <i
                      class="el-icon-edit operationi"
                      @click="editOperation(scoped.row)"
                    ></i>
                  </el-tooltip>

                  <el-tooltip effect="dark" content="重置reset" placement="top">
                    <i
                      class="el-icon-refresh-right operationi"
                      @click="resetOperation(scoped.row)"
                    ></i>
                  </el-tooltip>

                  <el-tooltip effect="dark" content="删除" placement="top">
                    <i
                      class="el-icon-delete operationi"
                      @click="deleteOperation(scoped.row)"
                    ></i>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="通知设置" name="3">
            <el-form
              :model="NotificationForm"
              :rules="NotificationRules"
              ref="NotificationForm"
              class="loginForm"
            >
              <el-form-item label="通知方式" prop="notificationMode">
                <el-select
                  v-model="NotificationForm.notificationMode"
                  clearable
                  placeholder="请选择通知方式"
                >
                  <el-option
                    v-for="item in notificationList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  @click="saveNotification('NotificationForm')"
                  >保存</el-button
                >
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="登录日志" name="4">
            <el-table
              v-if="activeName == 4"
              :data="loginLogData"
              stripe
              style="width: 100%"
            >
              <el-table-column type="index" label="序号" width="80">
              </el-table-column>
              <el-table-column label="登录时间" width="" align="center">
                <template slot-scope="scoped">
                  {{ scoped.row.loginDate | TimeconversionSs }}
                </template>
              </el-table-column>
              <el-table-column prop="" label="登录地址" width="" align="center">
              </el-table-column>
              <el-table-column prop="" label="登录IP" align="center">
              </el-table-column>
              <el-table-column prop="" label="登录设备" align="center">
              </el-table-column>
              <el-table-column label="登录状态" align="center">
                <template slot-scope="scoped">
                  <el-tag v-if="scoped.row.LoginStatus == 2" type="danger"
                    >失败</el-tag
                  >
                  <el-tag v-else type="success">成功</el-tag>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-card>
    </div>

    <!-- 应用设置编辑 -->
    <el-dialog
      title="编辑应用"
      :visible.sync="editOperationVisible"
      width="30%"
      @closed="editOperationClose('editOperationForm')"
    >
      <el-form
        :model="editOperationForm"
        :rules="editOperationRules"
        ref="editOperationForm"
      >
        <el-form-item label="名称" prop="name">
          <el-input
            v-model="editOperationForm.name"
            clearable
            placeholder="名称"
          ></el-input>
        </el-form-item>
        <el-form-item label="权限" prop="jurisdiction">
          <el-select
            v-model="editOperationForm.jurisdiction"
            clearable
            multiple
            placeholder="请选择权限"
          >
            <el-option
              v-for="item in jurisdictionList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOperationVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="sureEditOperation('editOperationForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "1",
      loginForm: {
        account: "",
        password: "",
      },
      loginRules: {
        account: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
      //应用设置表格
      applySettingsData: [
        {
          name: "1212",
          ClientID: "123123",
          ClientSecret: "ClientSecret123123",
          jurisdiction: [
            { a: "定时任务" },
            { a: "环境变量" },
            { a: "配置文件" },
          ],
        },
        {
          name: "1223",
          ClientID: "122322",
          ClientSecret: "ClientSecret122333",
          jurisdiction: [
            { a: "定时任务" },
            { a: "环境变量" },
            { a: "配置文件" },
          ],
        },
        {
          name: "1234",
          ClientID: "123422",
          ClientSecret: "ClientSecret123444",
          jurisdiction: [
            { a: "定时任务" },
            { a: "环境变量" },
            { a: "配置文件" },
          ],
        },
      ],
      //应用设置编辑弹窗
      editOperationVisible: false,
      editOperationForm: {
        name: "",
        jurisdiction: [],
      },
      editOperationRules: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        jurisdiction: [
          { required: true, message: "请选择权限", trigger: "blur" },
        ],
      },
      jurisdictionList: [
        { value: "1", label: "系统信息" },
        { value: "2", label: "依赖管理" },
        { value: "3", label: "任务日志" },
        { value: "4", label: "脚本管理" },
        { value: "5", label: "配置文件" },
        { value: "6", label: "环境变量" },
        { value: "7", label: "定时任务" },
      ],
      //通知设置
      NotificationForm: {
        notificationMode: "",
      },
      NotificationRules: {
        notificationMode: [
          { required: true, message: "请选择通知方式", trigger: "blur" },
        ],
      },
      notificationList: [{ value: "1", label: "1" }],
      //登录日志
      loginLogData: [
        {
          loginDate: 1661932187066,
          LoginStatus: 1,
        },
        {
          loginDate: 1661932189999,
          LoginStatus: 2,
        },
      ],
    };
  },
  created() {},
  mounted() {
    let a = new Date().getTime();
    console.log(a, "aaa");
  },
  methods: {
    handleClick() {},
    saveLogin(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    sureEditOperation(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    saveNotification(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    //应用设置编辑
    editOperation(row) {
      this.editOperationVisible = true;
    },
    editOperationClose(formName) {
      this.$refs[formName].resetFields();
    },
    //重置
    resetOperation(row) {
      const h = this.$createElement;
      this.$msgbox({
        title: "确认重置",
        message: h("p", null, [
          h("div", null, [
            h("span", null, "确认重置应用"),
            h("span", { style: "color: #fbad60" }, row.name),
            h("span", null, "的Secret吗?"),
          ]),
          h("div", null, [
            h(
              "div",
              { style: "color: #8c8c8c" },
              `重置${row.name}会让当前应用所有token失效`
            ),
          ]),
        ]),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        center: true,
      }).then(() => {
        //发请求
        this.$message({
          type: "info",
          message: "action: ",
        });
      });
    },
    deleteOperation(row) {
      const h = this.$createElement;
      this.$msgbox({
        title: "确认删除",
        message: h("p", null, [
          h("div", null, [
            h("span", null, "确认删除应用"),
            h("span", { style: "color: #fbad60" }, row.name),
            h("span", null, "吗?"),
          ]),
        ]),
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        center: true,
      }).then(() => {
        //发请求
        this.$message({
          type: "info",
          message: "action: ",
        });
      });
    },
    copyMesg(item) {
      // 模拟 输入框
      var cInput = document.createElement("input");
      cInput.value = item;
      document.body.appendChild(cInput);
      cInput.select(); // 选取文本框内容

      document.execCommand("copy");

      this.$message({
        type: "success",
        message: "复制成功",
      });
      // 复制成功后再将构造的标签 移除
      document.body.removeChild(cInput);
    },

    changeThemColor(type){
        console.log(type);
    }
  },
};
</script>

<style lang="less" scoped>
.loginForm {
  width: 30%;
}

.copyIcon {
  color: #1890ff;
  margin-left: 5px;
  cursor: pointer;
}

.jurisdictionTag {
  margin-right: 5px;
}

.operationi {
  margin-right: 10px;
  color: #1890ff;
  cursor: pointer;
  font-size: 20px;
}
</style>