<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">任务列表</div>
      <div class="rightButton">
        <el-input
          placeholder="请输入名称或者关键词"
          v-model="key"
          class="input-with-select"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getList"
          ></el-button>
        </el-input>
        <!-- <el-button type="primary" size="mini" @click="addNewTask">新建任务</el-button> -->
      </div>
    </div>
    <div class="mainMseeage">
      <!-- <div class="ChooseAll" v-if="ShowChooseAll">
        <el-button type="primary" @click="batch()">批量删除</el-button>
        <el-button type="primary" @click="batch()">批量启用</el-button>
        <el-button type="primary" @click="batch()">批量禁用</el-button>
        <el-button type="primary" @click="batch()">批量运行</el-button>
        <el-button type="primary" @click="batch()">批量停止</el-button>
        <el-button type="primary" @click="batch()">批量置顶</el-button>
        <el-button type="primary" @click="batch()">批量取消置顶</el-button>
        <el-button type="primary" @click="batch()">批量修改标签</el-button>
        <span style="margin-left: 10px">已选择<span style="color: #409eff">{{ choosedNum }}</span>项</span>
      </div> -->
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        v-loading="tableLoading"
        :height="tableHeight"
      >
        <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
        <el-table-column type="index" label="序号" width="80">
        </el-table-column>
        <el-table-column prop="name" label="任务名" width="" align="center">
          <template slot-scope="scoped">
            <el-button type="text">{{ scoped.row.name }}</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="command"
          label="任务"
          show-overflow-tooltip
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop=""
          label="操作"
          width="400"
          fixed="right"
          align="center"
        >
          <template slot-scope="scoped">
            <el-button type="text" @click="fdTask(scoped.row, 4)"
              >运行
            </el-button>
            <el-button type="text" @click="fdTask(scoped.row, 5)"
              >停止
            </el-button>
            <el-button type="text" @click.native="fdTask(scoped.row, 2)"
              >启用</el-button
            >
            <el-button type="text" @click.native="fdTask(scoped.row, 1)"
              >禁用</el-button
            >
            <el-button type="text" @click.native="fdTask(scoped.row, 3)"
              >置顶</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="changePage"
        :page-sizes="[10, 20, 30]"
        :page-size="changeSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal"
      >
      </el-pagination>
    </div>

    <!-- 新建任务 -->
    <el-dialog
      :title="newTitle"
      :visible.sync="newTaskVisible"
      width="30%"
      @close="handleClose('newTaskForm')"
    >
      <el-form
        :model="newTaskForm"
        :rules="newTaskrules"
        ref="newTaskForm"
        class="demo-ruleForm"
      >
        <el-form-item label="名称" prop="name">
          <el-input
            v-model="newTaskForm.name"
            placeholder="请输入任务名称"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="命令" prop="command">
          <el-input
            v-model="newTaskForm.command"
            placeholder="请输入要执行的命令"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="定时规则" prop="timingRule">
          <el-input
            v-model="newTaskForm.timingRule"
            placeholder="秒(可选) 分 时 天 月 周"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="标签" prop="remark">
          <el-input
            v-model="newTaskForm.remark"
            placeholder="请输入任务标签"
            clearable
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newTaskVisible = false">取 消</el-button>
        <el-button type="primary" @click="addNewTask('newTaskForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 运行青龙选择 -->
    <el-dialog
      :title="qlName"
      :visible.sync="qlChooseVisible"
      width="30%"
      @closed="qlChooseClosed"
    >
      <el-checkbox
        :indeterminate="isIndeterminate"
        v-model="qlCheckAll"
        @change="qlChooseAllChange"
        >全选</el-checkbox
      >
      <div style="margin: 15px 0"></div>
      <el-checkbox-group v-model="qlChooseList" @change="listChange">
        <el-checkbox
          v-for="(qlName, index) in qlList"
          :label="qlName.id"
          :key="index"
        >
          {{
            "url：" + qlName.url + " " + "状态：" + qlName.status
          }}</el-checkbox
        >
      </el-checkbox-group>

      <span slot="footer" class="dialog-footer">
        <el-button @click="qlChooseVisible = false">取 消</el-button>
        <el-button
          type="primary"
          :loading="sureQlChooseLoading"
          @click="sureQlChoose"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 日志 -->
    <el-dialog
      :title="journalName"
      :visible.sync="journalVisible"
      width="30%"
      :class="taskJournal"
      @close="journalClose"
    >
      <p v-for="(item, index) in taskJournalList" :key="index">{{ item }}</p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="journalVisible = false"
          >知道了</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from "element-ui";
import {
  queryScripts,
  runScript,
  stopScript,
  queryQl,
  disableScript,
  pinScript,
  enableScript,
} from "../axiosRequest/allRequest";

export default {
  data() {
    return {
      tableLoading: false,
      key: "",
      tableHeight: window.innerHeight - 120,
      ShowChooseAll: false,
      changePage: 1,
      changeSize: 10,
      tableTotal: 0,
      choosedNum: 0,
      newTaskVisible: false,
      newTitle: "新建任务",
      newTaskForm: {
        name: "",
        command: "",
        timingRule: "",
        remark: "",
      },
      tableData: [],
      newTaskrules: {
        name: [],
        remark: [],
        command: [
          { required: true, message: "请输入要执行的命令", trigger: "blur" },
        ],
        timingRule: [
          { required: true, message: "请输入定时规则", trigger: "blur" },
        ],
      },
      journal: "",
      journalVisible: false,
      taskJournal: "taskJournalOk",
      //   taskJournal: "taskJournaling",
      taskJournalList: [1, 2, 3],
      //日志定时器
      taskInterval: null,
      qlList: [],
      qlChooseVisible: false,
      isIndeterminate: true,
      qlCheckAll: false,
      qlChooseList: [],
      lineRow: {},
      sureQlChooseLoading: false,
      qlName: "选择运行青龙",
      qlType: 1,
      journalName: "日志",
    };
  },
  created() {
    this.getList();
    queryQl().then((res) => {
      if (res.code != 200) {
        Message.error("获取青龙数据异常！");
      } else {
        this.qlList = res.data;
      }
    });
  },
  mounted() {},
  methods: {
    qlChooseAllChange(val) {
      this.qlChooseList = [];
      if (val) {
        this.qlList.forEach((item, index) => {
          this.qlChooseList.push(item.id);
        });
      }
      this.isIndeterminate = val;
    },
    listChange(val) {
      let checkedCount = val.length;
      this.qlCheckAll = checkedCount === this.qlList.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.qlList.length;
    },
    sureQlChoose() {
      if (this.qlChooseList.length == 0)
        return Message.warning("请选择需要操作的青龙");
      let param = {
        ids: [...this.qlChooseList],
        command: this.lineRow.command,
      };
      this.sureQlChooseLoading = true;
      if (this.qlType == 5) {
        stopScript(param).then((res) => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName='停止日志'
          }
        });
      } else if (this.qlType == 4) {
        runScript(param).then((res) => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName='运行日志'
          }
        });
      } else if (this.qlType == 3) {
        pinScript(param).then((res) => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName='置顶日志'
          }
        });
      } else if (this.qlType == 2) {
        enableScript(param).then((res) => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName='启用日志'
          }
        });
      } else if (this.qlType == 1) {
        disableScript(param).then((res) => {
          this.sureQlChooseLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.qlChooseVisible = false;
            this.journalVisible = true;
            this.taskJournalList = res.data;
            this.journalName='禁用日志'
          }
        });
      }
    },
    qlChooseClosed() {
      this.lineRow = {};
      this.qlChooseList = [];
    },
    getList() {
      this.tableLoading = true;
      let param = {
        key: this.key,
        pageNo: this.changePage,
        pageSize: this.changeSize,
      };
      queryScripts(param).then((res) => {
        this.tableLoading = false;
        this.tableData = [];
        this.tableTotal = 0;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          this.tableData = res.data.scriptsList;
          this.tableTotal = res.data.total;
        }
      });
    },

    handleSelectionChange(val) {
      this.choosedNum = val.length;
      if (val.length > 0) {
        this.ShowChooseAll = true;
      } else {
        this.ShowChooseAll = false;
      }
    },
    handleSizeChange(val) {
      //一页多少条
      this.changeSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      //第几页
      this.changePage = val;
      this.getList();
    },
    handleClose(formName) {
      this.$refs[formName].resetFields();
    },
    addNewTask(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },

    fdTask(row, i) {
      this.qlChooseVisible = true;
      this.lineRow = row;
      this.qlChooseList = [];
      this.qlType = i;
      if (i == 1) {
        this.qlName = "选择禁用青龙";
      } else if (i == 2) {
        this.qlName = "选择启用的青龙";
      } else if (i == 3) {
        this.qlName = "选择置顶的青龙";
      } else if (i == 4) {
        this.qlName = "选择运行的青龙";
      } else if (i == 5) {
        this.qlName = "选择停止的青龙";
      }
      this.qlList.forEach((item) => {
        this.qlChooseList.push(item.id);
      });
      this.listChange(this.qlChooseList);
    },

    //日志定时器
    start() {
      this.taskInterval = setInterval(() => {
        //日志请求
        this.taskJournalList.push(1);
        if (1) {
          //如果获取到请求返回结束标志则关闭定时器
          //   this.end();
        }
      }, 1000);
    },

    end() {
      clearInterval(this.taskInterval);
      this.taskInterval = null;
    },

    detialTask(row) {
      this.journalVisible = true;
      this.journal = row.name;
      this.start();
    },
    journalClose() {
      this.end();
    },
    addNewTask() {
      this.newTaskVisible = true;
      this.newTitle = "新建任务";
    },

    editTask(row) {
      console.log(111);
      this.newTaskVisible = true;
      this.newTitle = "编辑任务";
      //row赋值
    },
    deleteTask(row) {},
    topTask(row) {},
  },
  //在离开面前之前必须手动清除定时器  否则定时器会一直存在
  beforeDestroy() {
    clearInterval(this.taskInterval);
  },
};
</script>

<style lang="less" scoped>
.el-pagination {
  background-color: #fff;
  text-align: right;
  padding: 10px;
}

.el-dropdown-link {
  color: #409eff;
  cursor: pointer;
}

.tagNoLoading::before {
  content: "";
  font-family: element-icons;
  font-size: 15px;
  width: 10px;
  height: 10px;
  margin-right: 10px;
}

::v-deep .taskJournaling {
  .el-dialog__title::before {
    content: "";
    width: 10px;
    /*先将loading区域变成正方形*/
    height: 10px;
    margin-right: 10px;
    display: inline-block;
    /*将loading区域变成行内元素，防止旋转的时候，100%宽度都在旋转*/
    border: 2px solid #f3f3f3;
    /*设置四周边框大小，并将颜色设置为浅白色*/
    border-top: 2px solid #4eacff;
    /*将上边框颜色设置为红色高亮，以便旋转的时候能够看到旋转的效果*/
    border-radius: 50%;
    /*将边框和内容区域都变成圆形*/
    animation: loading-360 0.8s infinite linear;
  }
}

::v-deep .taskJournalOk {
  .el-dialog__title::before {
    content: "";
    font-family: element-icons;
    width: 10px;
    /*先将loading区域变成正方形*/
    height: 10px;
    margin-right: 10px;
  }
}

.tagLoading::before {
  content: "";
  width: 10px;
  /*先将loading区域变成正方形*/
  height: 10px;
  margin-right: 10px;
  display: inline-block;
  /*将loading区域变成行内元素，防止旋转的时候，100%宽度都在旋转*/
  border: 1px solid #f3f3f3;
  /*设置四周边框大小，并将颜色设置为浅白色*/
  border-top: 1px solid #4eacff;
  /*将上边框颜色设置为红色高亮，以便旋转的时候能够看到旋转的效果*/
  border-radius: 50%;
  /*将边框和内容区域都变成圆形*/
  animation: loading-360 0.8s infinite linear;
}

@keyframes loading-360 {
  0% {
    transform: rotate(0deg);
    /*动画起始的时候旋转了0度*/
  }

  100% {
    transform: rotate(360deg);
    /*动画结束的时候旋转了360度*/
  }
}
</style>
