<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">线报活动</div>
      <div class="rightButton">
        <el-button type="primary" size="small" @click="addActivity(1)"
          >添加线报</el-button
        >
        <el-button type="primary" size="small" @click="xbActivity = true"
          >线报清单</el-button
        >
        <el-input
          placeholder="活动名称/脚本名称/洞察变量"
          v-model="key"
          clearable
          class="input-with-select"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getList"
          ></el-button>
        </el-input>
      </div>
    </div>

    <div class="mainMseeage">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        v-loading="tableLoading"
        :height="tableHeight"
      >
        <el-table-column type="index" label="序号" width="80">
        </el-table-column>
        <el-table-column
          prop="activityName"
          label="活动名称"
          width=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="script"
          label="脚本名称"
          show-overflow-tooltip
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="createdTime"
          label="新增时间"
          align="center"
        ></el-table-column>
        <el-table-column prop="updatedTime" label="更新时间" align="center">
        </el-table-column>
        <el-table-column label="操作" fixed="right" align="center">
          <template slot-scope="scoped">
            <el-button type="text" @click="getKey(scoped.row)"
              >洞察变量</el-button
            >
            <!-- <el-button type="text" @click="addNew('修改线报活动',scoped.row)">修改</el-button> -->
            <el-button type="text" @click="delScript(scoped.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-sizes="[10, 20, 30]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal"
      >
      </el-pagination>
    </div>

    <el-dialog
      title="洞察变量"
      :visible.sync="addNewVisible"
      width="30%"
      @closed="addNewClose"
    >
      <div>
        <span style="margin-right: 5px">活动名称&nbsp;&nbsp;&nbsp;</span
        ><el-input
          v-model="addNewForm.activityName"
          style="width: 65%; margin-right: 5px"
          clearable
          placeholder="请输入活动名称"
        ></el-input>
      </div>
      <div>
        <span style="margin-right: 5px">脚本名称&nbsp;&nbsp;&nbsp;</span
        ><el-input
          v-model="addNewForm.script"
          style="width: 65%; margin-right: 5px"
          clearable
          placeholder="请输入脚本名称"
        ></el-input>
      </div>
      <div v-for="(item, index) in addNewForm.keys" :key="index">
        <span style="margin-right: 5px">洞察变量{{ index + 1 }}:</span>
        <el-input
          style="width: 65%; margin-right: 5px"
          v-model="item.key"
          clearable
        ></el-input>
        <el-button class="adBtn" type="text" @click="add()">+</el-button>
        <el-button class="adBtn" type="text" @click="out(index)">-</el-button>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addNewVisible = false">取 消</el-button>
        <el-button type="primary" :loading="addLoading" @click="sureAddNew"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 线报清单新增 -->
    <el-dialog
      :show-close="false"
      :visible.sync="xbActivity"
      width="60%"
      class="cbDi"
      @closed="xbActivityClose"
      @open="xbActivityOpen"
    >
      <el-dialog
        width="30%"
        title="执行结果"
        :visible.sync="innerVisible"
        append-to-body
        @closed="colseNext"
      >
        <div v-for="(item, index) in this.lineRow" :key="index">{{ item }}</div>
      </el-dialog>

      <el-form
        :inline="true"
        :model="xbActivityForm"
        :rules="xbActivityRules"
        ref="xbActivityForm"
        style="text-align: center"
      >
        <el-form-item label="线报" prop="name">
          <el-input
            placeholder="请输入线报"
            v-model="xbActivityForm.name"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addxbActivity('xbActivityForm')"
            >新增</el-button
          >
          <el-button type="primary" @click="getQueryActivity">查询</el-button>
        </el-form-item>
      </el-form>

      <el-table
        :data="xbActivityData"
        v-loading="xbActivityLoading"
        style="width: 100%"
      >
        <el-table-column type="index" label="序号" width="100" align="center">
        </el-table-column>
        <el-table-column
          prop="content"
          label="线报活动内容"
          width=""
          align="center"
        >
        </el-table-column>
        <el-table-column label="执行结果" align="center" width="100">
          <template slot-scope="scoped">
            <el-button
              type="text"
              v-if="scoped.row.resultList"
              @click="getDetial(scoped.row.resultList)"
              >查询结果</el-button
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="createdTime"
          label="创建时间"
          width="200"
          align="center"
        >
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="nhandleSizeChange"
        @current-change="nhandleCurrentChange"
        :current-page="npageNo"
        :page-sizes="[10, 20, 30]"
        :page-size="npageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="ntableTotal"
      >
      </el-pagination>

      <span slot="footer" class="dialog-footer">
        <el-button @click="xbActivity = false">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 添加线报 -->
    <el-dialog
      title="添加线报"
      :visible.sync="addVisible"
      width="50%"
      @closed="addClose('addForm')"
    >
      <el-form
        :model="addForm"
        label-width="130px"
        :rules="addrules"
        ref="addForm"
      >
        <el-form-item label="activityName" prop="activityName">
          <el-input v-model="addForm.activityName"></el-input>
        </el-form-item>
        <el-form-item label="script" prop="script">
          <el-input v-model="addForm.script"></el-input>
        </el-form-item>
        <el-form-item
          v-for="(item, index) in addKeys"
          :key="index"
          :label="keyName + (index + 1 - 0)"
        >
          <el-input v-model="item.key" style="width: 80%"></el-input>
          <el-button
            style="font-size: 20px; font-weight: 600"
            @click="addKey"
            type="text"
            >+</el-button
          >
          <el-button
            style="font-size: 20px; font-weight: 600"
            @click="reduceKeys(index)"
            type="text"
            v-if="index != 0"
            >-</el-button
          >
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button
          type="primary"
          :loading="sureAddLoading"
          @click="sureAdd('addForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  queryWire,
  updateWire,
  deleteWire,
  queryActivity,
  addActivity,
  addWire,
} from "../axiosRequest/allRequest";

export default {
  data() {
    return {
      key: "",
      tableLoading: false,
      tableHeight: window.innerHeight - 120,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      tableTotal: 0,
      npageNo: 1,
      npageSize: 10,
      ntableTotal: 0,
      newName: "新增线报活动",
      addNewVisible: false,
      addNewForm: {
        addNewForm: "",
        script: "",
        keys: [{ key: "" }],
      },
      addLoading: false,
      xbActivity: false,
      xbActivityData: [],
      xbActivityLoading: false,
      xbActivityForm: {
        name: "",
      },
      xbActivityRules: {
        name: [
          { required: true, message: "新增时请输入线报", trigger: "blur" },
        ],
      },
      innerVisible: false,
      lineRow: [],
      addVisible: false,
      addKeys: [{ key: "" }],
      addForm: {
        activityName: "",
        script: "",
        keys: [],
      },
      addrules: {
        activityName: [
          { required: true, message: "请输入activityName", trigger: "blur" },
        ],
        script: [{ required: true, message: "请输入script", trigger: "blur" }],
      },
      keyName: "key",
      sureAddLoading: false,
    };
  },
  created() {
    this.getList();
  },
  methods: {
    getList() {
      this.tableLoading = true;
      let param = {
        key: this.key,
        pageNo: this.pageNo,
        pageSize: this.pageSize,
      };
      queryWire(param)
        .then((res) => {
          this.tableLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            this.tableData = res.data.wireList;
            this.tableTotal = res.data.total;
          }
        })
        .catch(() => {});
    },
    getKey(row) {
      this.addNewForm = JSON.parse(JSON.stringify(row));
      this.addNewVisible = true;
    },
    add() {
      this.addNewForm.keys.push({ key: "" });
    },
    out(index) {
      if (this.addNewForm.keys.length == 1)
        return Message.warning("最少保留一条数据");
      this.addNewForm.keys.splice(index, 1);
    },
    sureAddNew() {
      let a = false;
      this.addNewForm.keys.forEach((item, index) => {
        if (!item.key) {
          a = true;
        }
      });
      if (a) return Message.warning("有未填写完的环境变量！");
      let keys = [];
      this.addNewForm.keys.forEach((item, index) => {
        keys.push({ key: item.key });
      });
      let param = {
        activityName: this.addNewForm.activityName,
        script: this.addNewForm.script,
        id: this.addNewForm.id,
        keys: keys,
      };
      this.addLoading = true;
      updateWire(param)
        .then((res) => {
          this.addLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success(res.message);
            this.addNewVisible = false;
            this.getList();
          }
        })
        .catch((err) => {});
    },
    addNewClose() {
      this.addNewForm = {
        keys: [{ key: "" }],
      };
    },
    delScript(row) {
      this.$confirm("是否确认删除当前线报？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        deleteWire([row.id]).then((res) => {
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success(res.message);
            this.getList();
          }
        });
      });
    },

    xbActivityOpen() {
      this.getQueryActivity();
    },

    xbActivityClose() {
      //   this.xbActivityForm.reset();
      this.$refs["xbActivityForm"].resetFields();
      this.xbActivityData = [];
      this.npageNo = 1;
      this.npageSize = 10;
      this.ntableTotal = 0;
    },

    addxbActivity(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          addActivity(this.xbActivityForm.name).then((res) => {
            if (res.code != 200) {
              Message.error(res.message);
            } else {
              Message.success(res.message);
              this.getQueryActivity();
            }
          });
        } else {
          return false;
        }
      });
    },

    getQueryActivity() {
      this.xbActivityLoading = true;
      let param = {
        pageNo: this.npageNo,
        pageSize: this.npageSize,
      };
      queryActivity(param).then((res) => {
        this.xbActivityLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          this.xbActivityData = res.data.wireActivityList;
          this.ntableTotal = res.data.total;
        }
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.pageNo = val;
      this.getList();
    },

    nhandleSizeChange(val) {
      this.npageSize = val;
      this.getQueryActivity();
    },
    nhandleCurrentChange(val) {
      this.npageNo = val;
      this.getQueryActivity();
    },

    getDetial(row) {
      this.innerVisible = true;
      this.lineRow = row;
    },
    colseNext() {
      this.lineRow = [];
    },

    addActivity(i) {
      this.addVisible = true;
    },
    addClose(addForm) {
      this.addKeys = [{ key: "" }];
      this.$refs[addForm].resetFields();
      this.addForm = {
        activityName: "",
        script: "",
        keys: [],
      };
    },

    addKey() {
      this.addKeys.push({ key: "" });
    },
    reduceKeys(index) {
      this.addKeys.splice(index, 1);
    },
    sureAdd(addForm) {
      this.$refs[addForm].validate((valid) => {
        if (valid) {
          let a = false;
          this.addKeys.forEach((item, index) => {
            if (!item.key) {
              a = true;
            }
          });
          if (a) return Message.warning("请填写全key");
          this.addForm.keys = this.addKeys;
          this.sureAddLoading = true;
          addWire(this.addForm).then((res) => {
            this.sureAddLoading = false;
            if (res.code != 200) {
              Message.error(res.message);
            } else {
              Message.success("新增成功");
              this.addVisible = false;
            }
          });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.adBtn {
  font-size: 20px;
  font-weight: 600;
}

::v-deep .cbDi {
  .el-dialog__header {
    display: none;
  }
}
</style>