import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import MainPage from '../components/MainPage.vue'
import ScheduledTasks from '../components/ScheduledTasks.vue'
import EnvironmentVariable from '../components/EnvironmentVariable.vue'
import ConfigurationFile from '../components/ConfigurationFile.vue'
import ScriptManagement from '../components/ScriptManagement.vue'
import SystemSetting from '../components/SystemSetting.vue'
import AddQlong from '../components/AddQlong.vue'
import SystemParameter from '../components/SystemParameter.vue'
import InformantActivity from '../components/InformantActivity.vue'
import help from '../components/SubmitJd.vue'
import sheep from '../components/Sheepsheep.vue'
import BoosterPool from '../components/BoosterPool.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/Login'
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },
  {
    path: '/help',
    name: 'help',
    component: help
  },
  {
    path: '/sheep',
    name: 'sheep',
    component: sheep
  },
  {
    path: '/MainPage',
    name: 'MainPage',
    redirect: '/AddQlong',
    component: MainPage,
    children: [
      {
        path: '/AddQlong',
        name: 'AddQlong',
        component: AddQlong,
      },
      {
        path: '/ScheduledTasks',
        name: 'ScheduledTasks',
        component: ScheduledTasks,
      },
      {
        path: '/EnvironmentVariable',
        name: 'EnvironmentVariable',
        component: EnvironmentVariable,
      },
      {
        path: '/ConfigurationFile',
        name: 'ConfigurationFile',
        component: ConfigurationFile,
      },
      {
        path: '/ScriptManagement',
        name: 'ScriptManagement',
        component: ScriptManagement,
      },
      {
        path: '/SystemSetting',
        name: 'SystemSetting',
        component: SystemSetting,
      },
      {
        path: '/SystemParameter',
        name: 'SystemParameter',
        component: SystemParameter,
      },
      {
        path: '/InformantActivity',
        name: 'InformantActivity',
        component: InformantActivity,
      },
      {
        path: '/BoosterPool',
        name: 'BoosterPool',
        component: BoosterPool,
      }
    ]
  },
]


const router = new VueRouter({
  // mode: 'history',
  base: '/',
  routes
})

router.beforeEach((to, from, next) => {
  if (to.fullPath == '/Login') {
    sessionStorage.clear()
    next()
  } else {
    next()
  }

})

export default router
