<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">环境变量</div>
      <div class="rightButton">
        <el-input placeholder="请输入名称/值/备注" v-model="searchEnv" class="input-with-select">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <el-button type="primary" size="mini" @click="newEnvVisible = true">新建变量</el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <div class="ChooseAll" v-if="ShowChooseAll">
        <el-button type="primary" @click="batch()">批量修改变量名称</el-button>
        <el-button type="primary" @click="batch()">批量删除</el-button>
        <el-button type="primary" @click="batch()">批量启用</el-button>
        <el-button type="primary" @click="batch()">批量禁用</el-button>
        <span style="margin-left: 10px">已选择<span style="color: #409eff">{{ choosedNum }}</span>项</span>
      </div>
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
        @selection-change="handleSelectionChange" :height="tableHeight">
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column type="index" width="55" label="序号">
        </el-table-column>
        <el-table-column prop="" label="名称" width="" align="center">
        </el-table-column>
        <el-table-column prop="taskName" label="值" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="taskName" label="备注" width="" align="center">
        </el-table-column>
        <el-table-column prop="taskName" label="更新时间" width="" align="center">
        </el-table-column>

        <el-table-column prop="taskName" label="状态" width="" align="center">
          <template slot-scope="scoped">
            <el-tag type="danger">已禁用</el-tag>
            <el-tag type="success">已启用</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="" label="操作" width="" fixed="right" align="center">
          <template slot-scope="scoped">
            <el-tooltip class="item" effect="dark" content="编辑" placement="top">
              <el-button type="text" class="operationButton" @click="editEnv(scoped.row)"><i class="el-icon-edit"
                  style="color: #409eff"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip class="item" effect="dark" content="启用" placement="top">
              <el-button type="text" class="operationButton" @click="startEnv(scoped.row)"><i
                  class="el-icon-circle-check" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="禁用" placement="top">
              <el-button type="text" class="operationButton" @click="forbiddenEnv(scoped.row)"><i
                  class="el-icon-remove-outline" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="text" class="operationButton" @click="deleteEnv(scoped.row)"><i class="el-icon-delete"
                  style="color: #409eff"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="changePage"
        :page-sizes="[10, 20, 30]" :page-size="changeSize" layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal">
      </el-pagination>
    </div>

    <!-- 新建任务 -->
    <!-- label-position="top" -->
    <el-dialog title="新建变量" :visible.sync="newEnvVisible" width="30%" @close="handleClose('newEnvForm')">
      <el-form :model="newEnvForm" :rules="newTaskrules" ref="newEnvForm" class="demo-ruleForm">
        <el-form-item label="名称" prop="name">
          <el-input v-model="newEnvForm.name" placeholder="请输入环境变量名称" clearable></el-input>
        </el-form-item>
        <el-form-item label="自动拆分" prop="autoSplit">
          <el-tooltip class="item" effect="dark" content="多个依赖是否换行分割" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>

          <el-radio-group v-model="newEnvForm.autoSplit">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="2">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="值" prop="value">
          <el-input v-model="newEnvForm.value" placeholder="请输入环境变量值" clearable></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="newEnvForm.remark" placeholder="请输入备注" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newEnvVisible = false">取 消</el-button>
        <el-button type="primary" @click="addEnvTask('newEnvForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改 -->
    <el-dialog title="编辑变量" :visible.sync="editEnvVisible" width="30%" @closed="editEnvClose('editEnvForm')">
      <el-form :model="editEnvForm" :rules="editEnvRules" ref="editEnvForm" class="demo-ruleForm">
        <el-form-item label="名称" prop="name">
          <el-input v-model="editEnvForm.name" placeholder="请输入环境变量名称" clearable></el-input>
        </el-form-item>
        <el-form-item label="值" prop="value">
          <el-input v-model="editEnvForm.value" placeholder="请输入环境变量值" clearable></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="editEnvForm.remark" placeholder="请输入备注" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editEnvVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureEditEnv('editEnvForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from "element-ui";
export default {
  data() {
    return {
      searchEnv: "",
      ShowChooseAll: false,
      tableHeight: window.innerHeight - 120,
      changePage: 1,
      changeSize: 10,
      tableTotal: 0,
      choosedNum: 0,
      newEnvVisible: false,
      editEnvVisible: false,
      tableData: [
        {
          taskName: "1",
        },
        {
          taskName: "2",
        },
        {
          taskName: "2",
        },
      ],
      newEnvForm: {
        name: "",
        autoSplit: 1,
        value: "",
        remark: "",
      },
      editEnvForm: {
        name: "",
        value: "",
        remark: "",
      },
      newTaskrules: {
        name: [
          { required: true, message: "请输入环境变量名称", trigger: "blur" },
        ],
        autoSplit: [],
        value: [
          { required: true, message: "请输入环境变量值", trigger: "blur" },
        ],
        remark: [],
      },
      editEnvRules: {
        name: [
          { required: true, message: "请输入环境变量名称", trigger: "blur" },
        ],
        value: [
          { required: true, message: "请输入环境变量值", trigger: "blur" },
        ],
        remark: [],
      },
    };
  },
  created() { },
  mounted() { },
  methods: {
    handleSelectionChange(val) {
      this.choosedNum = val.length;
      if (val.length > 0) {
        this.ShowChooseAll = true;
      } else {
        this.ShowChooseAll = false;
      }
    },
    handleSizeChange(val) {
      //一页多少条
    },
    handleCurrentChange(val) {
      //第几页
    },
    handleClose(formName) {
      this.$refs[formName].resetFields();
    },
    editEnvClose(formName) {
      this.$refs[formName].resetFields();
    },
    addEnvTask(val) {
      this.$refs[val].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    editEnv(row) {
      //修改
      this.editEnvVisible = true;
    },
    sureEditEnv(val) {
      this.$refs[val].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    startEnv(row) {
      //启用
      this.$confirm("是否确认启用xxx?", "确认启用", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        Message.success("111");
      });
    },
    forbiddenEnv(row) {
      //禁用
      this.$confirm("是否确认禁用xxx?", "确认禁用", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        Message.success("222");
      });
    },
    deleteEnv(row) {
      //删除
      this.$confirm("是否确认删除xxx?", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        Message.success("333");
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-radio-group {
  width: 100%;
}
</style>