<script lang="ts">
export default {
  data() {
    return {
      indexActive: "AddQlong",
    };
  },
  created() {
    let a = sessionStorage.getItem("pathRouter");
    if (a) {
      this.indexActive = a;
    }
    sessionStorage.setItem("pathRouter", this.indexActive);
  },
  methods: {
    handleOpen(index) {
      this.indexActive = index;
      sessionStorage.setItem("pathRouter", this.indexActive);
    },
    logOut() {
      this.$confirm("是否确认退出当前登录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        sessionStorage.clear();
        this.$router.replace("/Login");
      });
    },
  },
};
</script>

<style lang="less" scoped>
::v-deep .el-menu-item-group__title {
